* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.parent {
  display: grid;
  grid-template-columns: repeat(3,1fr);
  gap: 20px;
  padding: 60px 100px;

}

.card {
  width: 100%;
}

.card img {
  width: 100%;
}

input {
  width: 50%;
  margin-left: 200px;
  margin-top: 100px;

}

.bola {
  display: flex;
  justify-content: center;
  gap: 20px;
}